<template>
    <div class="client-info">
        <div class="client-info-detail__nav plr-30 zsx-nav">
            <div class="icon iconfont iconreturn zsx-rt"
            @click="onBack">
            </div>
            <div class="title">添加商户</div>
        </div>
        <mu-container>
            <mu-form ref="form" label-position="left" :model="form" class="mu-demo-form">
                <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                 <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                 <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                 <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                 <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                 <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                 <mu-form-item prop="input" label="Input" :rules="usernameRules">
                    <mu-text-field v-model="form.input"></mu-text-field>
                </mu-form-item>
                <mu-form-item>
                    <mu-button color="primary" @click="submit">提交</mu-button>
                    <mu-button @click="clear">重置</mu-button>
                </mu-form-item>
            </mu-form>
        </mu-container>
    </div>
</template>
<script>
import { CLIENT_INFO} from "@/router/types";
export default {
    data() {
        return {
            usernameRules: [
                { validate: (val) => !!val, message: '必须填写用户名'},
                { validate: (val) => val.length >= 3, message: '用户名长度大于3'}
            ],
            form: {
                input: '',
            }
        }
    },
    methods: {
        submit () {
            this.$refs.form.validate().then((result) => {
                console.log('form valid: ', result)
            });
        },
        clear () {
            this.$refs.form.clear();
            this.form = {
                input: '',
            };
        },
        // 返回
        onBack() {
            this.$router.push({
                name: CLIENT_INFO
            });
        },
    }
}
</script>>


